import { useInView } from "react-intersection-observer";
import { motion, AnimatePresence } from "framer-motion";
import React, { useState, useEffect, useRef } from "react";
import "../styles/main.css";

// gallery     
const importAll = (r) => {
  let images = {};
  r.keys().map((item, index) => {
    images[item.replace('./', '')] = r(item);
    return null;
  });
  return images;
};

// assets/gallery    
const galleryImages = importAll(
  require.context('../assets/gallery', false, /\.(png|jpe?g|svg)$/i)
);

//    (  )
const allImages = Object.keys(galleryImages)
  .sort() //   
  .map((fileName, index) => ({
    id: index + 1,
    src: galleryImages[fileName],
    alt: `  ${index + 1}`,
    title: `  ${index + 1}`
  }));

const Gallery = () => {
  const { ref, inView } = useInView({ triggerOnce: true, threshold: 0.2 });
  const [selectedImage, setSelectedImage] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isExpanded, setIsExpanded] = useState(false);
  const [additionalImagesLoaded, setAdditionalImagesLoaded] = useState(false);
  const galleryGridRef = useRef(null);

  //      Intersection Observer
  const { ref: loadTriggerObserverRef, inView: nearEnd } = useInView({
    threshold: 0.1,
    triggerOnce: false
  });

  //   (  )
  const displayImages = isExpanded ? allImages : allImages.slice(0, 12);

  const openModal = (index) => {
    setCurrentIndex(index);
    setSelectedImage(allImages[index]);
  };

  const closeModal = () => {
    setSelectedImage(null);
  };

  const nextSlide = () => {
    const newIndex = (currentIndex + 1) % allImages.length;
    setCurrentIndex(newIndex);
    setSelectedImage(allImages[newIndex]);
  };

  const prevSlide = () => {
    const newIndex = (currentIndex - 1 + allImages.length) % allImages.length;
    setCurrentIndex(newIndex);
    setSelectedImage(allImages[newIndex]);
  };

  const toggleExpanded = () => {
    const wasExpanded = isExpanded;
    setIsExpanded(!isExpanded); // UI  
    
    //    ( )    
    if (!wasExpanded && !additionalImagesLoaded) {
      console.log('  -     ');
      const additionalImages = allImages.slice(12);
      
      //    (UI  )
      additionalImages.forEach(image => {
        const img = new Image();
        img.onload = () => {
          console.log(`  : ${image.id}`);
        };
        img.onerror = () => {
          console.log(`  : ${image.id}`);
        };
        img.src = image.src;
      });
      
      setAdditionalImagesLoaded(true); //   
    }
  };

  //        
  useEffect(() => {
    if (nearEnd && !isExpanded && !additionalImagesLoaded) {
      console.log('    -    ');
      const additionalImages = allImages.slice(12);
      
      const preloadAdditionalImages = async () => {
        const imagePromises = additionalImages.map((image) => {
          return new Promise((resolve, reject) => {
            const img = new Image();
            img.onload = resolve;
            img.onerror = reject;
            img.src = image.src;
          });
        });
        
        try {
          await Promise.all(imagePromises);
          console.log('    ');
          setAdditionalImagesLoaded(true);
        } catch (error) {
          console.log('    :', error);
        }
      };

      preloadAdditionalImages();
    }
  }, [nearEnd, isExpanded, additionalImagesLoaded]);

  //        
  useEffect(() => {
    const preloadImages = async () => {
      const imagePromises = allImages.map((image) => {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.onload = resolve;
          img.onerror = reject;
          img.src = image.src;
        });
      });
      
      try {
        await Promise.all(imagePromises);
        console.log('    ');
      } catch (error) {
        console.log('   :', error);
      }
    };

    //   
    const handleTouchStart = (e) => {
      // passive   
    };
    
    const handleTouchMove = (e) => {
      // passive   
    };

    preloadImages();
    
    // passive   
    document.addEventListener('touchstart', handleTouchStart, { passive: true });
    document.addEventListener('touchmove', handleTouchMove, { passive: true });

    return () => {
      document.removeEventListener('touchstart', handleTouchStart);
      document.removeEventListener('touchmove', handleTouchMove);
    };
  }, []); //      

  return (
    <motion.section
      className="gallery"
      ref={ref}
      initial={{ opacity: 0, y: 40 }}
      animate={inView ? { opacity: 1, y: 0 } : {}}
      transition={{ duration: 0.8 }}
    >
      <h2></h2>
      
      <div className="gallery-grid" ref={galleryGridRef}>
        {displayImages.map((image, index) => (
          <motion.div
            key={image.id}
            className="gallery-item"
            onClick={() => openModal(index)}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            initial={{ opacity: 0, y: 20 }}
            animate={inView ? { opacity: 1, y: 0 } : {}}
            transition={{ 
              duration: index < 12 ? 0.6 : 0.3, //     
              delay: index < 12 ? index * 0.1 : 0 //      
            }}
          >
            <img 
              src={image.src} 
              alt={image.alt}
              loading={index < 12 ? "lazy" : "eager"}
              decoding="async"
            />
          </motion.div>
        ))}
      </div>

      {/*        (    ) */}
      {!isExpanded && (
        <div 
          ref={loadTriggerObserverRef}
          style={{ height: '1px', marginTop: '1rem' }}
        />
      )}

      {/* /  */}
      <motion.button
        className="toggle-btn"
        onClick={toggleExpanded}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        style={{ marginTop: '2rem' }}
      >
        {isExpanded ? '' : ''} 
        <span className={`arrow ${isExpanded ? 'up' : 'down'}`}></span>
      </motion.button>

      {/*   */}
      <AnimatePresence>
        {selectedImage && (
          <motion.div
            className="gallery-modal"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={closeModal}
          >
            <motion.div
              className="modal-content"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              onClick={(e) => e.stopPropagation()}
            >
              <button className="modal-close" onClick={closeModal}>
                
              </button>
              
              <button className="modal-btn prev" onClick={prevSlide}>
                &#8249;
              </button>
              
              <div className="modal-image-container">
                <AnimatePresence mode="wait">
                  <motion.img
                    key={currentIndex}
                    src={selectedImage.src}
                    alt={selectedImage.alt}
                    initial={{ opacity: 0, x: 100 }}
                    animate={{ opacity: 1, x: 0 }}
                    exit={{ opacity: 0, x: -100 }}
                    transition={{ duration: 0.3 }}
                  />
                </AnimatePresence>
              </div>
              
              <button className="modal-btn next" onClick={nextSlide}>
                &#8250;
              </button>
              
              <div className="modal-counter">
                {currentIndex + 1} / {allImages.length}
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.section>
  );
};

export default Gallery; 